<template>
  <div>
    <el-table :data="list">
      <el-table-column prop="_id" label="ID" width="250"></el-table-column>
      <el-table-column prop="title" label="" width="250"></el-table-column>
      <el-table-column label="" width="250" prop="cate.name"></el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
          <el-button
            @click="$router.push(`/strategies/edit/${scope.row._id}`)"
            type="text"
            size="small"
          ></el-button>
          <el-button type="text" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("/admin/api/rest/strategies");
      this.list = res.data;
    },
    async remove(row) {
      await this.$confirm(`  ${row.title}?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          await this.$http.delete(`/admin/api/rest/strategies/${row._id}`);
          this.$message({
            type: "success",
            message: "!"
          });
          this.fetch();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  created() {
    this.fetch();
  }
};
</script>